import './style.scss'
import CatalogSection from './components/CatalogSection.vue';

const { store } = B2BPortal;
const { createStore, types } = B2BPortal.Vue.Store.List;

export class CatalogSectionShowcase
{
	constructor(element, params)
	{
		this.params = params;
		this.namespace = params.namespace;

		this.element = element;

		this.initializeStore();
		this.bindEvents();
		this.attachTemplate();
	}

	initializeStore()
	{
		const state = {
			id: this.params.id,
			items: this.params.items,
			pagination: this.params.pagination,
			iblock: this.params.iblock
		};
		
		store.registerModule(this.namespace, createStore(state));
	}

	bindEvents()
	{
		BX.addCustomEvent(`filter-${this.params.filterName}-on-submit`, this.onFilterSubmit.bind(this));
		BX.addCustomEvent(`filter-${this.params.filterName}-on-reset`, this.onFilterReset.bind(this));
	}

	onFilterSubmit(params, url)
	{
		if (url)
		{
			this.params.ajaxUrl = url;
		}

		store.dispatch(`${this.namespace}/fetch`, {
			url: url || this.params.ajaxUrl,
			params
		})
		.then(res => {
			if (res.status === 'success' && params)
			{
				store.commit(
					`${this.namespace}/${types.SAVE_PARAMS}`, 
					params
				);
			}
		});
	}
	
	onFilterReset(params, url)
	{
		this.onFilterSubmit(params, url);
	}

	attachTemplate()
	{
		const Component = Vue.extend({
			...CatalogSection,
			store
		});

		this.vm = new Component({
			propsData: {
				namespace: this.namespace
			}
		});

		this.vm.$mount(this.element, true);
	}
}